import React, { useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { ApproachContext } from '../context/ApproachContext';
import "../css/AbandonApproachLink.css"

function AbandonApproachLink({ to, currentStep, children }) {
    const navigate = useNavigate();
    const { approachData } = useContext(ApproachContext);

    const unsavedChanges = currentStep.currentStep > (approachData.maxStepCompleted || 0);


    const handleClick = (e) => {
        e.preventDefault();
        if (unsavedChanges) {
            const confirmLeave = window.confirm(
                "Do you really want to abandon the approach without saving the changes?"
            );
            if (confirmLeave) {
                navigate(to);
            }
        } else {
            navigate(to);
        }
    };

    return (
        <button type="button" onClick={handleClick} className='nav-link abandon-link'>
            {children}
        </button>
    );
}

export default AbandonApproachLink;