import React, { useState } from 'react';
import '../css/UserStoryTemplate.css';

const UserStoryTemplate = ({ onApply }) => {
    const [role, setRole] = useState('');
    const [action, setAction] = useState('');
    const [benefit, setBenefit] = useState('');

    const handleSubmit = (e) => {
        e.preventDefault();
        const userStory = `As ${role}, I want to ${action} so that ${benefit}.`;
        onApply(userStory);
        setRole('');
        setAction('');
        setBenefit('');
    };

    return (
        <div className="user-story-template-card">
            <h3>Create New User Story</h3>
            <form onSubmit={handleSubmit} className="user-story-template-form">
                <div className="form-group">
                    <label>As:</label>
                    <input
                        type="text"
                        value={role}
                        onChange={(e) => setRole(e.target.value)}
                        required
                    />
                </div>
                <div className="form-group">
                    <label>I want to:</label>
                    <input
                        type="text"
                        value={action}
                        onChange={(e) => setAction(e.target.value)}
                        required
                    />
                </div>
                <div className="form-group">
                    <label>So that:</label>
                    <input
                        type="text"
                        value={benefit}
                        onChange={(e) => setBenefit(e.target.value)}
                        required
                    />
                </div>
                <button type="submit" className="apply-btn">
                    Insert
                </button>
            </form>
        </div>
    );
};

export default UserStoryTemplate;
