import React, { useContext, useState, useEffect } from 'react';
import { ApproachContext } from '../context/ApproachContext';
import NavigationButtons from '../common/NavigationButtons';
import TechniquesSelection from "../common/TechniquesSelection";
import { useUnsavedChanges } from '../context/useUnsavedChanges';
import Navbar from "../common/Navbar";
import '../css/ApproachesSelectionPage.css';
import ProgressBar from "../common/ProgressBar";

const ApproachesSelectionPage = () => {
    const { approachData, updateStepData } = useContext(ApproachContext);
    const currentStep = 2;
    const totalPages = 9;
    const maxStep = approachData.maxStepCompleted || 0;
    useUnsavedChanges(currentStep, maxStep);

    const [selectedTechniques, setSelectedTechniques] = useState([]);
    const [questions, setQuestions] = useState([]);

    useEffect(() => {
        if (approachData.step2) {
            setSelectedTechniques(approachData.step2.selectedTechniques || []);
            setQuestions(approachData.step2.questions || []);
        }
    }, [approachData.step2]);

    const handleTechniqueChange = (technique) => {
        if (selectedTechniques.includes(technique)) {
            setSelectedTechniques(selectedTechniques.filter(t => t !== technique));
        } else {
            setSelectedTechniques([...selectedTechniques, technique]);
        }
    };

    const addQuestion = () => {
        setQuestions([...questions, { questionText: '', associatedTechniques: [] }]);
    };

    const updateQuestionText = (index, text) => {
        const updatedQuestions = questions.map((q, i) =>
            i === index ? { ...q, questionText: text } : q
        );
        setQuestions(updatedQuestions);
    };

    const toggleQuestionTechnique = (questionIndex, technique) => {
        const updatedQuestions = questions.map((q, i) => {
            if (i === questionIndex) {
                const currentTechniques = q.associatedTechniques || [];
                if (currentTechniques.includes(technique)) {
                    return { ...q, associatedTechniques: currentTechniques.filter(t => t !== technique) };
                } else {
                    return { ...q, associatedTechniques: [...currentTechniques, technique] };
                }
            }
            return q;
        });
        setQuestions(updatedQuestions);
    };

    const removeQuestion = (index) => {
        setQuestions(questions.filter((_, i) => i !== index));
    };

    const handleSave = () => {
        updateStepData('step2', { selectedTechniques, questions });
    };

    return (
        <>
            <Navbar currentStep={currentStep}/>
            <ProgressBar currentStep={currentStep} totalSteps={totalPages}/>
            <div className="page-container start-page">
                <h2>2. Choose the Techniques for the Collection and Identification of Objectives</h2>
                <p>Choose the Techniques you want to use and define questions to ask in these approaches.</p>
                <div className="section">
                    <h3>Techniques</h3>
                    <TechniquesSelection
                        selectedTechniques={selectedTechniques}
                        setSelectedTechniques={setSelectedTechniques}
                    />
                </div>

                <div className="section">
                    <h3>Questions for Applying the Techniques</h3>
                    <p>Associate each question with one or more of the techniques selected above:</p>
                    {questions.map((question, index) => (
                        <div key={index} className="question-item">
                            <input
                                type="text"
                                placeholder="Type the question"
                                value={question.questionText}
                                onChange={(e) => updateQuestionText(index, e.target.value)}
                            />
                            <div className="question-techniques">
                                {selectedTechniques.length > 0 ? (
                                    selectedTechniques.map((technique, idx) => (
                                        <label key={idx} className="technique-label">
                                            <input
                                                type="checkbox"
                                                checked={question.associatedTechniques.includes(technique)}
                                                onChange={() => toggleQuestionTechnique(index, technique)}
                                            />
                                            {technique}
                                        </label>
                                    ))
                                ) : (
                                    <p className="no-techniques">
                                        Select at least one of the above techniques.
                                    </p>
                                )}
                            </div>
                            <button className="remove-btn" onClick={() => removeQuestion(index)}>
                                Delete Question
                            </button>
                        </div>
                    ))}
                    <button className="add-btn" onClick={addQuestion}>Add Question</button>
                </div>

                <NavigationButtons
                    onSave={handleSave}
                    onBeforeDownload={handleSave}
                    nextPath="/step3"
                    previousPath="/step1"
                    stepNumber={currentStep}
                />
            </div>
        </>
    );
};

export default ApproachesSelectionPage;
