import React, { useContext } from 'react';
import { ApproachContext } from '../context/ApproachContext';
import '../css/DownloadButton.css';

const DownloadButton = ({ inline, onBeforeDownload }) => {
    const context = useContext(ApproachContext);
    if (!context) {
        console.error('Undefined');
        return null;
    }
    const { approachDataRef } = context;

    const downloadJSON = () => {
        if (onBeforeDownload) {
            onBeforeDownload();
        }
        const dataStr = JSON.stringify(approachDataRef.current, null, 2);
        const blob = new Blob([dataStr], { type: 'application/json' });
        const url = URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = 'approachData.json';
        link.click();
        URL.revokeObjectURL(url);
    };

    return (
        <button className={inline ? "download-button-inline" : "download-button"} onClick={downloadJSON}>
            Download JSON
        </button>
    );
};

export default DownloadButton;
