import React, { useState } from 'react';
import catalogData from '../catalog/catalog.json';
import '../css/CatalogPage2.css';
import NavbarOtherPages from "../common/NavbarOtherPages";

const CatalogPage2 = () => {
    const [searchQuery, setSearchQuery] = useState('');
    const [expandedItemId, setExpandedItemId] = useState(null);

    const filterItem = (item) => {
        const query = searchQuery.toLowerCase().trim();
        if (!query) return true;
        return item.title.toLowerCase().includes(query);
    };

    return (
        <>
            <NavbarOtherPages />
            <div className="catalog-page">
                <div className="search-container">
                    <input
                        type="text"
                        placeholder="Search by title..."
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                    />
                </div>

                <div className="catalog-content">
                    {Object.keys(catalogData).map((category) => (
                        <div key={category} className="catalog-category">
                            <h2 className="category-title">{category}</h2>
                            <div className="catalog-items">
                                {catalogData[category]
                                    .filter(filterItem)
                                    .map((item) => (
                                        <div
                                            key={item.id}
                                            className={`catalog-item ${expandedItemId === item.id ? 'expanded' : ''}`}
                                            onClick={() => setExpandedItemId(expandedItemId === item.id ? null : item.id)}
                                        >
                                            <div className="item-header">
                                                <h3>{item.title}</h3>
                                            </div>
                                            {expandedItemId === item.id && (
                                                <div className="item-details">
                                                    <p><strong>Goal:</strong> {item.goal}</p>
                                                    <p><strong>Sub Goal:</strong> {item.subGoal}</p>
                                                    <p>
                                                        <strong>Requirements:</strong>
                                                        <br/>
                                                        {Array.isArray(item.requirements) ? (
                                                            item.requirements.map((req, index) => (
                                                                <span key={index}>
                                                                    {req.text} ({req.type})
                                                                    <br/>
                                                                </span>
                                                                ))
                                                                ) : (
                                                                <span>
                                                                    {item.requirements.text} ({item.requirements.type})
                                                                <br/>
                                                                </span>
                                                        )}
                                                    </p>
                                                    <p><strong>User Story:</strong> {item.userStory}</p>
                                                    <p><strong>BDD Scenario:</strong> {item.bddScenario}</p>
                                                </div>
                                            )}
                                        </div>
                                    ))}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </>
    );
};

export default CatalogPage2;
