import React, { useContext, useState, useEffect } from 'react';
import { ApproachContext } from '../context/ApproachContext';
import NavigationButtons from '../common/NavigationButtons';
import Navbar from '../common/Navbar';
import BDDBlockTemplate from '../common/BDDBlockTemplate';
import ProgressBar from "../common/ProgressBar";
import { useUnsavedChanges } from "../context/useUnsavedChanges";
import '../css/BDDScenariosPage.css';

const BDDScenariosPage = () => {
    const { approachData, updateStepData } = useContext(ApproachContext);
    const currentStep = 7;
    const totalPages = 9;
    const maxStep = approachData.maxStepCompleted || 0;
    let approachDataVariable = approachData.step6;
    useUnsavedChanges(currentStep, maxStep);

    const [objectives, setObjectives] = useState([]);
    const [activeTemplate, setActiveTemplate] = useState({});
    const [editMode, setEditMode] = useState({});
    const [activeTemplateGlobal, setActiveTemplateGlobal] = useState({});
    const [editModeGlobal, setEditModeGlobal] = useState({});
    const [globalEditValues, setGlobalEditValues] = useState({});

    useEffect(() => {
        if (approachData.step7?.objectives) {
            console.log("entrou1")
            const updatedObjectives = approachData.step7.objectives.map(objective => ({
                ...objective,
                bddScenario: objective.bddScenario || '',
                globalBDDRejected: objective.globalBDDRejected || false,
                requirements: objective.requirements.map(req => ({
                    ...req,
                    bddScenario: req.bddScenario || ''
                }))
            }));
            setObjectives(updatedObjectives);
            approachDataVariable = approachData.step7;
        }
        else if (approachData.step6?.objectives) {
            console.log("entrou2")
            const updatedObjectives = approachData.step6.objectives.map(objective => ({
                ...objective,
                bddScenario: objective.bddScenario || '',
                globalBDDRejected: objective.globalBDDRejected || false,
                requirements: objective.requirements.map(req => ({
                    ...req,
                    bddScenario: req.bddScenario || ''
                }))
            }));
            setObjectives(updatedObjectives);
            approachDataVariable = approachData.step6;
        }
    }, [approachDataVariable]);

    const updateGlobalBDDScenario = (objectiveId, value) => {
        setObjectives(prev =>
            prev.map(obj =>
                obj.id === objectiveId ? { ...obj, bddScenario: value } : obj
            )
        );
    };

    const updateBDDScenario = (objectiveId, requirementId, value) => {
        setObjectives(prev =>
            prev.map(obj =>
                obj.id === objectiveId
                    ? {
                        ...obj,
                        requirements: obj.requirements.map(req =>
                            req.id === requirementId ? { ...req, bddScenario: value } : req
                        )
                    }
                    : obj
            )
        );
    };

    const toggleTemplate = (reqId) => {
        setActiveTemplate(prev => ({ ...prev, [reqId]: !prev[reqId] }));
    };

    const toggleEditMode = (reqId) => {
        setEditMode(prev => ({ ...prev, [reqId]: !prev[reqId] }));
    };

    const toggleTemplateGlobal = (objectiveId) => {
        setActiveTemplateGlobal(prev => ({ ...prev, [objectiveId]: !prev[objectiveId] }));
    };

    const toggleEditModeGlobal = (objectiveId) => {
        setGlobalEditValues(prev => ({
            ...prev,
            [objectiveId]: objectives.find(o => o.id === objectiveId)?.bddScenario || ''
        }));
        setEditModeGlobal(prev => ({ ...prev, [objectiveId]: true }));
    };

    const confirmGlobalEdit = (objectiveId) => {
        updateGlobalBDDScenario(objectiveId, globalEditValues[objectiveId] || '');
        setEditModeGlobal(prev => ({ ...prev, [objectiveId]: false }));
    };

    const cancelGlobalEdit = (objectiveId) => {
        setGlobalEditValues(prev => ({
            ...prev,
            [objectiveId]: objectives.find(o => o.id === objectiveId)?.bddScenario || ''
        }));
        setEditModeGlobal(prev => ({ ...prev, [objectiveId]: false }));
    };

    const rejectGlobalBDDScenario = (objectiveId) => {
        const rejectedText = "Main BDD Scenario from the catalog was rejected.";
        updateGlobalBDDScenario(objectiveId, rejectedText);
        setEditModeGlobal(prev => ({ ...prev, [objectiveId]: false }));
        setActiveTemplateGlobal(prev => ({ ...prev, [objectiveId]: false }));
        setObjectives(prev =>
            prev.map(obj =>
                obj.id === objectiveId ? { ...obj, globalBDDRejected: true } : obj
            )
        );
    };

    const handleApplyTemplate = (objectiveId, requirementId, bddText) => {
        updateBDDScenario(objectiveId, requirementId, bddText);
        setActiveTemplate(prev => ({ ...prev, [requirementId]: false }));
        setEditMode(prev => ({ ...prev, [requirementId]: true }));
    };

    const handleApplyTemplateGlobal = (objectiveId, bddText) => {
        updateGlobalBDDScenario(objectiveId, bddText);
        setActiveTemplateGlobal(prev => ({ ...prev, [objectiveId]: false }));
        setEditModeGlobal(prev => ({ ...prev, [objectiveId]: false }));
        setGlobalEditValues(prev => ({ ...prev, [objectiveId]: bddText }));
    };

    const handleSave = () => {
        const allObjectivesValid = objectives.every(obj =>
            obj.requirements && obj.requirements.every(req => req.bddScenario && req.bddScenario.trim() !== '')
        );

        if (!allObjectivesValid) {
            alert("Each requirement must have a defined BDD Scenario.");
            return;
        }

        updateStepData('step7', { objectives });

        const mergeObjectives = (existingObjectives = [], newObjectives) => {
            const merged = existingObjectives.map(existingObj => {
                const newObj = newObjectives.find(obj => obj.id === existingObj.id);
                if (newObj) {
                    const existingReqs = existingObj.requirements || [];
                    const newReqs = newObj.requirements || [];
                    const mergedReqs = existingReqs.map(existingReq => {
                        const newReq = newReqs.find(req => req.id === existingReq.id);
                        if (newReq) {
                            return {
                                ...existingReq,
                                bddScenario:
                                    (newReq.bddScenario && newReq.bddScenario.trim() !== "" &&
                                        (!existingReq.bddScenario || existingReq.bddScenario.trim() === ""))
                                        ? newReq.bddScenario
                                        : existingReq.bddScenario,
                            };
                        }
                        return existingReq;
                    });
                    newReqs.forEach(newReq => {
                        if (!existingReqs.some(req => req.id === newReq.id)) {
                            mergedReqs.push(newReq);
                        }
                    });
                    return { ...existingObj, requirements: mergedReqs };
                }
                return existingObj;
            });

            newObjectives.forEach(newObj => {
                if (!existingObjectives.some(obj => obj.id === newObj.id)) {
                    merged.push(newObj);
                }
            });

            return merged;
        };

        if (Array.isArray(approachData.step8?.objectives) && approachData.step8.objectives.length > 0) {
            updateStepData('step8', {
                objectives: mergeObjectives(approachData.step8.objectives, objectives)
            });
        }
        if (Array.isArray(approachData.step9?.objectives) && approachData.step9.objectives.length > 0) {
            updateStepData('step9', {
                objectives: mergeObjectives(approachData.step9.objectives, objectives)
            });
        }
    };


    return (
        <>
            <Navbar currentStep={currentStep} />
            <ProgressBar currentStep={currentStep} totalSteps={totalPages} />
            <div className="page-container bdd-scenarios-page">
                <h2>7. Create BDD Scenario</h2>
                <p>
                    Transform User Stories into BDD Scenarios using the Gherkin (Given-When-Then) syntax.
                </p>

                {objectives.length > 0 ? (
                    objectives.map((objective) => (
                        <div key={objective.id} className="objective-section">
                            <h3>{objective.goal}</h3>
                            {objective.subGoal && <p className="sub-goal">Sub Goal: {objective.subGoal}</p>}

                            {objective.fromCatalog && (
                                <div className="global-bdd-card">
                                    <h4>Main BDD Scenario</h4>
                                    {objective.globalBDDRejected ? (
                                        <div className="global-bdd-display">
                                            <span>Main BDD Scenario from the catalog was rejected.</span>
                                        </div>
                                    ) : (
                                        <>
                                            {editModeGlobal[objective.id] ? (
                                                <div className="global-edit-container">
                                                    <input
                                                        type="text"
                                                        value={globalEditValues[objective.id] || ""}
                                                        onChange={(e) =>
                                                            setGlobalEditValues(prev => ({ ...prev, [objective.id]: e.target.value }))
                                                        }
                                                        placeholder="Insert the BDD Scenario"
                                                    />
                                                    <button className="confirm-btn" onClick={() => confirmGlobalEdit(objective.id)}>
                                                        Ok
                                                    </button>
                                                    <button className="cancel-btn" onClick={() => cancelGlobalEdit(objective.id)}>
                                                        Cancel
                                                    </button>
                                                </div>
                                            ) : (
                                                <div className="global-bdd-display">
                                                    <span>{objective.bddScenario || "No scenario has been defined."}</span>
                                                    <div className="global-buttons">
                                                        <button className="edit-btn" onClick={() => toggleEditModeGlobal(objective.id)}>
                                                            Edit
                                                        </button>
                                                        <button className="reject-btn" onClick={() => rejectGlobalBDDScenario(objective.id)}>
                                                            Reject
                                                        </button>
                                                    </div>
                                                </div>
                                            )}
                                            <button className="template-btn" onClick={() => toggleTemplateGlobal(objective.id)}>
                                                {activeTemplateGlobal[objective.id] ? 'Close Template' : 'Use Template'}
                                            </button>
                                            {activeTemplateGlobal[objective.id] && (
                                                <BDDBlockTemplate onApply={(bddText) => handleApplyTemplateGlobal(objective.id, bddText)} />
                                            )}
                                        </>
                                    )}
                                </div>
                            )}

                            {(!objective.fromCatalog || (objective.fromCatalog && objective.requirements.some(req => req.userStory))) && (
                                <div className="requirements-section">
                                    <h4>BDD Scenarios for User Stories</h4>
                                    {objective.requirements.length > 0 ? (
                                        objective.requirements.map((req) => (
                                            <div key={req.id} className="bdd-card global-edit-container">
                                                <label>User Story: {req.userStory || "No User Story"}</label>
                                                {req.bddScenario ? (
                                                    <input
                                                        value={req.bddScenario}
                                                        onChange={(e) => updateBDDScenario(objective.id, req.id, e.target.value)}
                                                        placeholder="Insert the BDD Scenario"
                                                    />
                                                ) : (
                                                    <button onClick={() => toggleTemplate(req.id)}>
                                                        Create BDD Scenario
                                                    </button>
                                                )}
                                                {activeTemplate[req.id] && (
                                                    <BDDBlockTemplate onApply={(bddText) => handleApplyTemplate(objective.id, req.id, bddText)} />
                                                )}
                                            </div>
                                        ))
                                    ) : (
                                        <p className="no-requirements">No User Story associated with a requirement.</p>
                                    )}
                                </div>
                            )}
                        </div>
                    ))
                ) : (
                    <p className="empty-message">No requirements were defined in the previous step.</p>
                )}

                <NavigationButtons onSave={handleSave} nextPath="/step8" previousPath="/step6" stepNumber={currentStep} onBeforeDownload={handleSave} />
            </div>
        </>
    );
};

export default BDDScenariosPage;
