import React, {useContext, useState} from 'react';
import { useNavigate } from 'react-router-dom';
import './css/HomePage.css';
import StepCard from "./common/StepCard";
import {ApproachContext} from "./context/ApproachContext";
import LoadProjectModal from "./common/LoadProjectModal";

const steps = [
    {
        stepNumber: 1,
        title: "Project Identification",
        description: ` Project Name: Define the name of the project.
 Overall Goal: Identify the overall goal of the IMS.
 Project Scope: Define the area or scope of the project.
 Stakeholders: List all key stakeholders involved in the project.`
    },
    {
        stepNumber: 2,
        title: "Choose Approaches for Gathering Goals",
        description: ` Select Techniques: Define which techniques are most appropriate from the available options.
 Define Questions: Develop the questions that will guide the application of these techniques and specify the target audience for these questions.`
    },
    {
        stepNumber: 3,
        title: "Initiate Goal Collection",
        description: ` Apply Techniques: Implement the chosen techniques to gather information.
 Document Findings: Record the information obtained through each technique.
 Consult the Catalog: Explore available general objectives in the catalog and validate them with stakeholders.
 Assign Stakeholders: Document which stakeholder is associated with each objective.`
    },
    {
        stepNumber: 4,
        title: "Refine Goals",
        description: ` Review Goals: Revisit and review the collected goals.
 Decompose Goals: Break down each goal into more detailed sub-goals.`
    },
    {
        stepNumber: 5,
        title: "Convert Goals into Requirements",
        description: ` Define Requirements: Establish both functional and non-functional requirements.
   Functional Requirements: Derive these from the sub-objectives.
   Non-Functional Requirements: Identify additional requirements that ensure performance, security, etc.`
    },
    {
        stepNumber: 6,
        title: "Create User Stories",
        description: ` Develop User Stories: For every requirement, create a user story to capture the end-user's perspective and needs.`
    },
    {
        stepNumber: 7,
        title: "Define BDD Scenarios",
        description: ` Establish Scenarios: For each user story, define Behavior-Driven Development (BDD) scenarios that outline the expected behaviors and outcomes.`
    },
    {
        stepNumber: 8,
        title: "Develop Prototypes",
        description: ` Prototype Creation: Build prototypes that implement the requirements as envisioned by the stakeholders, allowing them to visualize the solution.`
    },
    {
        stepNumber: 9,
        title: "Gather Feedback",
        description: ` Collect Feedback: Record the feedback received from stakeholders after reviewing the requirements and prototypes.
 Iterate if Needed: If necessary, revisit the requirements definition phase to refine the requirements and continue the process until satisfactory feedback is achieved.`
    }
];

const HomePage = () => {
    const navigate = useNavigate();
    const { resetApproachData } = useContext(ApproachContext);
    const [showCatalog, setShowCatalog] = useState(false);
    const [modalOpen, setModalOpen] = useState(false);

    const openModal = () => {
        setModalOpen(true);
    };

    const closeModal = () => {
        setModalOpen(false);
    };

    const handleShowCatalog = () => setShowCatalog(true);
    const handleCloseCatalog = () => setShowCatalog(false);

    const handleStartNewApproach = () => {
        resetApproachData();
        navigate('/step1');
    };

    return (
        <div className="homepage-container">
            <header className="hero">
                <h1>Welcome to the Tool for the Requirements Approach for Interactive Multimedia Systems</h1>
                <p>
                    Discover this 9-step approach to collect and organize your project requirements for Interactive Multimedia Systems.
                </p>
                <div className="hero-buttons">
                    <button className="start-button" onClick={handleStartNewApproach}>
                        New Project
                    </button>
                    <button onClick={openModal} className="load-button">
                        Load Saved Project
                    </button>
                    <button className="load-button" onClick={() => navigate('/catalog')}>
                        Catalog
                    </button>
                </div>
                {modalOpen && <LoadProjectModal isOpen={modalOpen} onClose={closeModal}/>}
            </header>

            {/* Introduction */}
            <section className="introduction">
                <h2>How It Works?</h2>
                <p>
                    Our tool guides you through a structured process, ensuring that no detail is overlooked. Each step helps you gather and analyze essential information for your project.
                </p>
                <p>
                    The steps in this approach cover everything from the first phase, where you can gather the main project information, to the last step, where you analyze the feedback and view all the information gathered during this process.
                </p>
                <p>
                    The main purpose of this tool is to help you gather information and visualize it in an organized way. There are steps that cannot be done in this tool (e.g. the steps related to prototyping), but appear in the flow of the approach in this tool.
                </p>
                <p>
                    Using the tool is not mandatory in order to use the approach. You can use it if you follow the steps below. You can consult a Catalog at the option in the start and bottom of the page which has examples of Patterns with Goals, Sub-goals, Requirements, User Stories and reusable BDD Scenarios that you can use as inspiration for your project.
                </p>
                <p>
                    We hope you enjoy using this approach!
                </p>
            </section>

            <section className="steps-overview">
                <h2>The 9-Step Approach</h2>
                <div className="steps-grid">
                    {steps.map((step) => (
                        <StepCard
                            key={step.stepNumber}
                            stepNumber={step.stepNumber}
                            title={step.title}
                            description={step.description}
                        />
                    ))}
                </div>
            </section>

            <section className="catalog-section">
                <h2>Reusable Requirements Catalog</h2>
                <p>
                    Consult our catalog of reusable requirement patterns. Each pattern includes a Goal, Sub Goal, Requirements, User Story, and BDD Scenario.
                    You can review all available patterns on a dedicated page.
                </p>
                <button className="catalog-button" onClick={() => navigate('/catalog')}>
                    View Catalog
                </button>
            </section>
        </div>
    );
};

export default HomePage;
