import React, { useState } from 'react';
import '../css/BDDBlockTemplate.css';

const BDDBlockTemplate = ({ onApply }) => {
    const [given, setGiven] = useState('');
    const [when, setWhen] = useState('');
    const [then, setThen] = useState('');

    const handleSubmit = (e) => {
        e.preventDefault();
        const bddScenario = `Given ${given} \nWhen ${when} \nThen ${then}`;
        onApply(bddScenario);
        setGiven('');
        setWhen('');
        setThen('');
    };

    return (
        <div className="bdd-block-template">
            <h3>Create BDD Scenario</h3>
            <form onSubmit={handleSubmit} className="bdd-block-form">
                <div className="form-group">
                    <label>Given:</label>
                    <input
                        type="text"
                        value={given}
                        onChange={(e) => setGiven(e.target.value)}
                        required
                    />
                </div>
                <div className="form-group">
                    <label>When:</label>
                    <input
                        type="text"
                        value={when}
                        onChange={(e) => setWhen(e.target.value)}
                        required
                    />
                </div>
                <div className="form-group">
                    <label>Then:</label>
                    <input
                        type="text"
                        value={then}
                        onChange={(e) => setThen(e.target.value)}
                        required
                    />
                </div>
                <button type="submit" className="apply-btn">Insert</button>
            </form>
        </div>
    );
};

export default BDDBlockTemplate;
