import React, { useState } from "react";
import "../css/TechniquesSelection.css";

const techniques = [
    "Interviews",
    "Questionnaires",
    "Observation",
    "Literature Review"
];

const TechniquesSelection = ({ selectedTechniques, setSelectedTechniques }) => {
    const toggleSelection = (technique) => {
        const updatedSelection = selectedTechniques.includes(technique)
            ? selectedTechniques.filter((t) => t !== technique)
            : [...selectedTechniques, technique];

        setSelectedTechniques(updatedSelection);
    };

    return (
        <div className="technique-option-container">
            {techniques.map((technique) => (
                <button
                    key={technique}
                    className={`technique-option ${
                        selectedTechniques.includes(technique) ? "selected" : ""
                    }`}
                    onClick={() => toggleSelection(technique)}
                >
                    {technique}
                </button>
            ))}
        </div>
    );
};

export default TechniquesSelection;
