import React, { useContext, useState, useEffect } from 'react';
import { ApproachContext } from '../context/ApproachContext';
import Navbar from '../common/Navbar';
import ProgressBar from '../common/ProgressBar';
import NavigationButtons from '../common/NavigationButtons';
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';
import '../css/FinalSummaryPage.css';

const FinalSummaryPage = () => {
    const { approachData, updateStepData } = useContext(ApproachContext);
    const currentStep = 9;
    const totalSteps = 9;
    const [objectives, setObjectives] = useState([]);

    useEffect(() => {
        if (approachData.step9?.objectives) {
            setObjectives(approachData.step9.objectives);
        }
    }, [approachData.step9]);

    const generatePDF = () => {
        const input = document.getElementById('summary-content');

        html2canvas(input, { scale: 2 }).then((canvas) => {
            const imgData = canvas.toDataURL('image/png');
            const pdf = new jsPDF('p', 'mm', 'a4');
            const pdfWidth = pdf.internal.pageSize.getWidth();
            const pdfHeight = pdf.internal.pageSize.getHeight();

            let imgWidth = pdfWidth;
            let imgHeight = (canvas.height * imgWidth) / canvas.width;

            let heightLeft = imgHeight;
            let position = 0;

            pdf.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
            heightLeft -= pdfHeight;

            while (heightLeft > 0) {
                position -= pdfHeight;
                pdf.addPage();
                pdf.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
                heightLeft -= pdfHeight;
            }

            pdf.save('project-summary.pdf');
        });
    };

    const handleSave = () => {
        updateStepData('step9', { objectives });
    };

    return (
        <>
            <Navbar currentStep={currentStep} />
            <ProgressBar currentStep={currentStep} totalSteps={totalSteps} />
            <div className="page-container final-summary-page">
                <h2>Final Project Summary</h2>
                <p className="intro">
                    Review all the information collected so far. This summary includes your objectives, sub-goals,
                    requirements, User Stories, and BDD Scenarios.
                </p>

                <div className="action-buttons">
                    <button className="pdf-btn" onClick={generatePDF}>
                        Download PDF
                    </button>
                </div>

                <div className="information" id="summary-content">
                    <div className="project-info-card">
                        <h4>Project Information</h4>
                        <p><strong>Project Name:</strong> {approachData.step1.projectName}</p>
                        <p><strong>General Objective:</strong> {approachData.step1.generalObjective}</p>
                        <p><strong>Project Area:</strong> {approachData.step1.projectArea}</p>
                        <p><strong>Stakeholders:</strong> {approachData.step1.stakeholders.join(", ")}</p>
                    </div>

                    <div className="techniques-card">
                        <h4>Selected Techniques</h4>
                        <p>{approachData.step2.selectedTechniques.join(", ")}</p>
                    </div>

                    <div className="questions-card">
                        <h4>Questions and Associated Techniques</h4>
                        {approachData.step2.questions.map((q, index) => (
                            <div key={index}>
                                <p><strong>Question {index + 1}:</strong> {q.questionText}</p>
                                <p><strong>Techniques:</strong> {q.associatedTechniques.join(", ")}</p>
                            </div>
                        ))}
                    </div>

                    <div className="objectives-container">
                        {objectives.length > 0 ? (
                            objectives.map((obj, index) => (
                                <div key={obj.id} className="objective-card">
                                    <div className="objective-header">
                                        <h3>{obj.goal}</h3>
                                        {obj.fromCatalog && <span className="catalog-badge">Catalog</span>}
                                    </div>

                                    {obj.description && (
                                        <p className="objective-description">{obj.description}</p>
                                    )}

                                    {obj.subObjectives && obj.subObjectives.length > 0 && (
                                        <div className="subobjectives-section">
                                            <h4>Sub-Goals</h4>
                                            <ul>
                                                {obj.subObjectives.map(sub => (
                                                    <li key={sub.id}>{sub.text}</li>
                                                ))}
                                            </ul>
                                        </div>
                                    )}

                                    {obj.requirements && obj.requirements.length > 0 && (
                                        <div className="requirements-section">
                                            <h4>Requirements</h4>
                                            {obj.requirements.map(req => (
                                                <div key={req.id} className="requirement-detail">
                                                    <p><strong>Requirement:</strong> {req.text}</p>
                                                    <p><strong>Type:</strong> {req.type}</p>
                                                    <p><strong>User Story:</strong> {req.userStory}</p>
                                                    <p><strong>BDD Scenario:</strong> {req.bddScenario}</p>
                                                    <p><strong>Feedback:</strong> {req.feedback.comment} ({req.feedback.classification})</p>
                                                </div>
                                            ))}
                                        </div>
                                    )}
                                </div>
                            ))
                        ) : (
                            <p className="empty-message">No objectives available.</p>
                        )}
                    </div>
                </div>
                <NavigationButtons
                    onSave={handleSave}
                    nextPath="/"
                    previousPath="/step9"
                    stepNumber={currentStep}
                />
            </div>

        </>
    );
};

export default FinalSummaryPage;
