import React, { useState } from 'react';
import '../css/Navbar.css';
import AbandonApproachLink from '../common/AbandonApproachLink';
import LoadProjectModal from "./LoadProjectModal";

const Navbar = (currentStep) => {
    const [modalOpen, setModalOpen] = useState(false);

    const openModal = () => {
        setModalOpen(true);
    };

    const closeModal = () => {
        setModalOpen(false);
    };

    return (
        <nav className="navbar navbar-expand-lg navbar-dark" style={{ background: 'linear-gradient(135deg, #4e54c8, #8f94fb)' }}>
            <div className="container-fluid">
                <span className="navbar-brand">
                    <AbandonApproachLink to="/" currentStep={currentStep} children='IMS Approach Tool'></AbandonApproachLink>
                </span>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav ms-auto">
                        <li className="nav-item">
                            <AbandonApproachLink to="/" currentStep={currentStep} children='Homepage'></AbandonApproachLink>
                        </li>
                        <li className="nav-item">
                            <AbandonApproachLink to="/catalog" currentStep={currentStep} children='Catalog'></AbandonApproachLink>
                        </li>
                        <li className="nav-item">
                            <button onClick={openModal} className='nav-link upload-link'>
                                Upload
                            </button>
                        </li>
                        <li className="nav-item">
                            <AbandonApproachLink to="/step1" currentStep={currentStep} children='New Project'></AbandonApproachLink>
                        </li>
                    </ul>
                </div>
            </div>
            {modalOpen && <LoadProjectModal isOpen={modalOpen} onClose={closeModal} />}
        </nav>
    );
};

export default Navbar;
