import React, { createContext, useState, useRef, useEffect } from 'react';

const defaultApproachData = {
    step1: {},
    step2: {},
    step3: {},
    step4: {},
    step5: {},
    step6: {},
    step7: {},
    step8: {},
    step9: {},
    maxStepCompleted: 0,
};

export const ApproachContext = createContext(null);

export const ApproachProvider = ({ children }) => {
    const [approachData, setApproachData] = useState(defaultApproachData);
    const approachDataRef = useRef(approachData);

    useEffect(() => {
        approachDataRef.current = approachData;
    }, [approachData]);

    const updateStepData = (step, data) => {
        setApproachData(prev => {
            const newData = {
                ...prev,
                [step]: { ...prev[step], ...data },
            };
            approachDataRef.current = newData;
            return newData;
        });
    };

    const resetApproachData = () => {
        setApproachData(defaultApproachData);
    };

    const updateMaxStepCompleted = (stepNumber) => {
        setApproachData(prev => ({
            ...prev,
            maxStepCompleted: Math.max(prev.maxStepCompleted || 0, stepNumber),
        }));
    };

    const loadApproachData = (data) => {
        setApproachData(data);
        approachDataRef.current = data;
    };

    const contextValue = {
        approachData,
        updateStepData,
        approachDataRef,
        updateMaxStepCompleted,
        resetApproachData,
        loadApproachData
    };

    return (
        <ApproachContext.Provider value={contextValue}>
            {children}
        </ApproachContext.Provider>
    );
};
