import React, { useContext, useState, useEffect } from 'react';
import { ApproachContext } from '../context/ApproachContext';
import NavigationButtons from '../common/NavigationButtons';
import Navbar from '../common/Navbar';
import ProgressBar from "../common/ProgressBar";
import { v4 as uuidv4 } from 'uuid';
import { useUnsavedChanges } from "../context/useUnsavedChanges";
import '../css/RequirementsPage.css';

const RequirementsPage = () => {
    const { approachData, updateStepData } = useContext(ApproachContext);
    const currentStep = 5;
    const totalPages = 9;
    const maxStep = approachData.maxStepCompleted || 0;
    let approachDataVariable = approachData.step4;
    useUnsavedChanges(currentStep, maxStep);

    const [objectives, setObjectives] = useState([]);

    useEffect(() => {
        if (approachData.step5?.objectives) {
            const updatedObjectives = approachData.step5.objectives.map((objective) => {
                if (objective.fromCatalog) {
                    return {
                        ...objective,
                        requirements: objective.requirements || [],
                    };
                } else {
                    if ((!objective.requirements || objective.requirements.length === 0) &&
                        objective.subObjectives && objective.subObjectives.length > 0) {
                        const derivedRequirements = objective.subObjectives.map((sub) => ({
                            id: uuidv4(),
                            text: '',
                            subObjectiveId: sub.id,
                            type: ""
                        }));
                        return {
                            ...objective,
                            requirements: derivedRequirements,
                        };
                    }
                    return objective;
                }
            });
            setObjectives(updatedObjectives);
            approachDataVariable = approachData.step5;
        }
        else if (approachData.step4?.objectives) {
            const updatedObjectives = approachData.step4.objectives.map((objective) => {
                if (objective.fromCatalog) {
                    return {
                        ...objective,
                        requirements: objective.requirements || [],
                    };
                } else {
                    if ((!objective.requirements || objective.requirements.length === 0) &&
                        objective.subObjectives && objective.subObjectives.length > 0) {
                        const derivedRequirements = objective.subObjectives.map((sub) => ({
                            id: uuidv4(),
                            text: '',
                            subObjectiveId: sub.id,
                            type: ""
                        }));
                        return {
                            ...objective,
                            requirements: derivedRequirements,
                        };
                    }
                    return objective;
                }
            });
            setObjectives(updatedObjectives);
            approachDataVariable = approachData.step4;
        }
    }, [approachDataVariable]);

    const handleSave = () => {
        updateStepData('step5', { objectives });

        const mergeRequirements = (existingObjectives, newObjectives) => {
            return existingObjectives.map(existingObj => {
                const newObj = newObjectives.find(o => o.id === existingObj.id);
                if (!newObj) return existingObj;

                const existingRequirements = existingObj.requirements || [];
                const newRequirements = newObj.requirements || [];

                const updatedRequirements = [
                    ...existingRequirements,
                    ...newRequirements.filter(newReq =>
                        !existingRequirements.some(oldReq => oldReq.id === newReq.id)
                    )
                ];

                return { ...existingObj, requirements: updatedRequirements };
            });
        };


        if (approachData.step6.objectives?.length > 0) {
            updateStepData('step6', {
                objectives: mergeRequirements(approachData.step6.objectives || [], objectives)
            });
        }
        if (approachData.step7.objectives?.length > 0) {
            updateStepData('step7', {
                objectives: mergeRequirements(approachData.step7.objectives || [], objectives)
            });
        }
        if (approachData.step8.objectives?.length > 0) {
            updateStepData('step8', {
                objectives: mergeRequirements(approachData.step8.objectives || [], objectives)
            });
        }
        if (approachData.step9.objectives?.length > 0) {
            updateStepData('step9', {
                objectives: mergeRequirements(approachData.step9.objectives || [], objectives)
            });
        }
    };

    const addRequirement = (objectiveId, subObjectiveId) => {
        setObjectives(objectives.map(obj => {
            if (obj.id === objectiveId) {
                const newRequirement = {
                    id: uuidv4(),
                    text: '',
                    subObjectiveId,
                    type: ""
                };
                return { ...obj, requirements: [...(obj.requirements || []), newRequirement] };
            }
            return obj;
        }));
    };

    const removeRequirement = (objectiveId, requirementId) => {
        setObjectives(objectives.map(obj =>
            obj.id === objectiveId
                ? { ...obj, requirements: obj.requirements.filter(req => req.id !== requirementId) }
                : obj
        ));
    };

    const updateRequirementText = (objectiveId, requirementId, text) => {
        setObjectives(objectives.map(obj =>
            obj.id === objectiveId
                ? {
                    ...obj,
                    requirements: obj.requirements.map(req =>
                        req.id === requirementId ? { ...req, text } : req
                    )
                }
                : obj
        ));
    };

    const updateRequirementType = (objectiveId, requirementId, type) => {
        setObjectives(objectives.map(obj =>
            obj.id === objectiveId
                ? {
                    ...obj,
                    requirements: obj.requirements.map(req =>
                        req.id === requirementId ? { ...req, type } : req
                    )
                }
                : obj
        ));
    };

    return (
        <>
            <Navbar currentStep={currentStep} />
            <ProgressBar currentStep={currentStep} totalSteps={totalPages} />
            <div className="page-container requirements-page">
                <h2>5. Convert Sub Goals into Requirements</h2>
                <p>Review each objective, view its sub-goals, and create or edit requirements accordingly. Specify whether each requirement is Functional or Non-Functional.</p>

                {objectives.length > 0 ? (
                    objectives.map((objective) => (
                        <div key={objective.id} className="objective-card">
                            <div className="objective-header">
                                <h4>Objective: {objective.goal}</h4>
                                {objective.fromCatalog && <span className="catalog-tag">From Catalog</span>}
                            </div>
                            {objective.subObjectives && objective.subObjectives.length > 0 && (
                                <div className="subobjectives-section">
                                    {objective.subObjectives.map((sub) => (
                                        <div key={sub.id} className="subobjective-section">
                                            <h5>Sub Goal: {sub.text}</h5>
                                            <div className="requirements-list">
                                                {objective.requirements && objective.requirements.filter(req => req.subObjectiveId === sub.id).length > 0 ? (
                                                    objective.requirements.filter(req => req.subObjectiveId === sub.id).map((req) => (
                                                        <div key={req.id} className="requirement-item">
                                                            <input
                                                                type="text"
                                                                value={req.text}
                                                                onChange={(e) => updateRequirementText(objective.id, req.id, e.target.value)}
                                                                placeholder="Enter requirement"
                                                            />
                                                            <div className="requirement-type">
                                                                <label>
                                                                    <input
                                                                        type="radio"
                                                                        name={`req-type-${req.id}`}
                                                                        value="Functional"
                                                                        checked={req.type === "Functional"}
                                                                        onChange={(e) => updateRequirementType(objective.id, req.id, e.target.value)}
                                                                    />
                                                                    Functional
                                                                </label>
                                                                <label>
                                                                    <input
                                                                        type="radio"
                                                                        name={`req-type-${req.id}`}
                                                                        value="Non-Functional"
                                                                        checked={req.type === "Non-Functional"}
                                                                        onChange={(e) => updateRequirementType(objective.id, req.id, e.target.value)}
                                                                    />
                                                                    Non-Functional
                                                                </label>
                                                            </div>
                                                            <button className="remove-requirement-btn" onClick={() => removeRequirement(objective.id, req.id)}>
                                                                Delete
                                                            </button>
                                                        </div>
                                                    ))
                                                ) : (
                                                    <p className="empty-message">No requirements defined for this sub-goal.</p>
                                                )}
                                                <button className="add-requirement-btn" onClick={() => addRequirement(objective.id, sub.id)}>
                                                    Add Requirement for this Sub Goal
                                                </button>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            )}
                        </div>
                    ))
                ) : (
                    <p className="empty-message">No objectives added yet.</p>
                )}

                <NavigationButtons
                    onSave={handleSave}
                    nextPath="/step6"
                    previousPath="/step4"
                    stepNumber={currentStep}
                    onBeforeDownload={handleSave}
                />
            </div>
        </>
    );
};

export default RequirementsPage;
