import React, { useContext, useState } from 'react';
import { ApproachContext } from '../context/ApproachContext';
import '../css/LoadProjectModal.css';
import {useNavigate} from "react-router-dom";

const LoadProjectModal = ({ isOpen, onClose }) => {
    const { loadApproachData } = useContext(ApproachContext);
    const [error, setError] = useState('');
    const navigate = useNavigate();

    if (!isOpen) return null;

    const validateData = (data) => {
        const requiredKeys = ['step1', 'step2', 'step3', 'step4', 'step5', 'step6', 'step7', 'step8', 'step9', 'maxStepCompleted'];
        return requiredKeys.every(key => Object.prototype.hasOwnProperty.call(data, key));
    };

    const handleFileChange = (e) => {
        const file = e.target.files[0];
        if (!file) return;

        // Verifica se o arquivo  JSON pelo tipo ou pela extenso
        if (file.type !== 'application/json' && !file.name.endsWith('.json')) {
            setError('Please select a valid JSON file.');
            return;
        }

        const reader = new FileReader();
        reader.onload = (event) => {
            try {
                const parsedData = JSON.parse(event.target.result);
                if (!validateData(parsedData)) {
                    setError('The JSON file does not have the expected format.');
                    return;
                }

                loadApproachData(parsedData);
                setError('');
                onClose();
                navigate('/step1');
            } catch (err) {
                setError('Error parsing the JSON file.');
            }
        };

        reader.readAsText(file);
    };

    return (
        <div className="load-project-modal-overlay">
            <div className="load-project-modal">
                <h2>Upload Project</h2>
                <p>Select the project's JSON file to load the data.</p>
                <input type="file" accept="application/json" onChange={handleFileChange} />
                {error && <p className="error-message">{error}</p>}
                <button onClick={onClose} className="close-btn">Close</button>
            </div>
        </div>
    );
};

export default LoadProjectModal;
