import React, { useState } from 'react';
import catalogData from '../catalog/catalog.json';
import '../css/CatalogModal.css';

const CatalogModal = ({ onClose, onSelectPattern }) => {
    const categories = Object.keys(catalogData);
    const [selectedCategory, setSelectedCategory] = useState(categories[0]);
    const [searchTerm, setSearchTerm] = useState('');

    const handleSelect = (pattern) => {
        if (onSelectPattern) {
            onSelectPattern(pattern);
        }
        onClose();
    };

    const filteredPatterns = catalogData[selectedCategory].filter((pattern) => {
        const lowerSearch = searchTerm.toLowerCase();
        return (
            pattern.title.toLowerCase().includes(lowerSearch) ||
            pattern.goal.toLowerCase().includes(lowerSearch) ||
            pattern.subGoal.toLowerCase().includes(lowerSearch)
        );
    });

    return (
        <div className="catalog-modal-overlay">
            <div className="catalog-modal">
                <div className="modal-header">
                    <h2>Select Requirement Pattern</h2>
                    <button className="close-button" onClick={onClose}>&times;</button>
                </div>
                <div className="modal-tabs">
                    {categories.map((category) => (
                        <button
                            key={category}
                            className={`tab-button ${selectedCategory === category ? 'active' : ''}`}
                            onClick={() => {
                                setSelectedCategory(category);
                                setSearchTerm('');
                            }}
                        >
                            {category}
                        </button>
                    ))}
                </div>
                <div className="modal-search">
                    <input
                        type="text"
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                        placeholder="Search patterns..."
                    />
                </div>
                <div className="modal-content">
                    {filteredPatterns.length > 0 ? (
                        filteredPatterns.map((pattern) => (
                            <div key={pattern.id} className="catalog-pattern">
                                <h3>{pattern.title}</h3>
                                <p><strong>Goal:</strong> {pattern.goal}</p>
                                <p><strong>Sub Goal:</strong> {pattern.subGoal}</p>
                                <p>
                                    <strong>Requirements:</strong>
                                    <br/>
                                    {Array.isArray(pattern.requirements) ? (
                                        pattern.requirements.map((req, index) => (
                                            <span key={index}>
                                                {req.text} ({req.type})
                                                <br/>
                                            </span>
                                        ))
                                        ) : (
                                        <span>
                                            {pattern.requirements.text} ({pattern.requirements.type})
                                            <br/>
                                        </span>
                                    )}
                                </p>
                                <p><strong>User Story:</strong> {pattern.userStory}</p>
                                <p><strong>BDD Scenario:</strong> {pattern.bddScenario}</p>
                                <button className="select-button" onClick={() => handleSelect(pattern)}>
                                    Select This Pattern
                                </button>
                            </div>
                        ))
                    ) : (
                        <p className="empty-message">No patterns found.</p>
                    )}
                </div>
            </div>
        </div>
    );
};

export default CatalogModal;
