import React, { useContext, useState, useEffect } from 'react';
import { ApproachContext } from '../context/ApproachContext';
import NavigationButtons from '../common/NavigationButtons';
import Navbar from '../common/Navbar';
import ProgressBar from '../common/ProgressBar';
import { Link } from 'react-router-dom';
import { useUnsavedChanges } from '../context/useUnsavedChanges';
import '../css/FeedbackPage.css';

const FeedbackPage = () => {
    const { approachData, updateStepData } = useContext(ApproachContext);
    const currentStep = 9;
    const totalSteps = 9;
    const maxStep = approachData.maxStepCompleted || 0;
    useUnsavedChanges(currentStep, maxStep);
    let approachDataVariable = approachData.step8;

    const [objectives, setObjectives] = useState([]);

    useEffect(() => {
        if (approachData.step9?.objectives) {
            setObjectives(approachData.step9.objectives);
            approachDataVariable = approachData.step9;
        }
        else if (approachData.step8?.objectives) {
            setObjectives(approachData.step8.objectives);
            approachDataVariable = approachData.step8;
        }
    }, [approachDataVariable]);

    const updateFeedbackItem = (objectiveId, requirementId, field, value) => {
        setObjectives(prev =>
            prev.map(obj =>
                obj.id === objectiveId
                    ? {
                        ...obj,
                        requirements: obj.requirements.map(req =>
                            req.id === requirementId
                                ? { ...req, feedback: { ...req.feedback, [field]: value } }
                                : req
                        )
                    }
                    : obj
            )
        );
    };

    const handleSave = () => {
        updateStepData('step9', { objectives });
    };

    return (
        <>
            <Navbar currentStep={currentStep} />
            <ProgressBar currentStep={currentStep} totalSteps={totalSteps} />
            <div className="page-container feedback-page">
                <h2>Feedback on Requirements</h2>
                <p className="intro">
                    For each requirement below, provide your comments and classify the requirement as <strong>Done</strong> or <strong>Need Changes</strong>.
                    If you choose "Need Changes", a link will be provided to edit the requirement in Step 5.
                </p>

                {objectives.length > 0 ? (
                    objectives.map((obj) => (
                        <div key={obj.id} className="feedback-objective-card">
                            <h3>Objective: {obj.goal}</h3>
                            {obj.requirements && obj.requirements.length > 0 ? (
                                <div className="requirements-feedback">
                                    {obj.requirements.map((req) => {
                                        const fb = req.feedback || { comment: "", classification: "" };
                                        return (
                                            <div key={req.id} className="feedback-requirement-item">
                                                <p className="requirement-info">
                                                    <strong>Requirement:</strong> {req.text} <span className="req-type">({req.type})</span>
                                                </p>
                                                <textarea
                                                    className="feedback-textarea"
                                                    value={fb.comment}
                                                    onChange={(e) =>
                                                        updateFeedbackItem(obj.id, req.id, 'comment', e.target.value)
                                                    }
                                                    placeholder="Enter your comment..."
                                                />
                                                <div className="classification">
                                                    <label>
                                                        <input
                                                            type="radio"
                                                            name={`feedback-${req.id}`}
                                                            value="Done"
                                                            checked={fb.classification === "Done"}
                                                            onChange={(e) =>
                                                                updateFeedbackItem(obj.id, req.id, 'classification', e.target.value)
                                                            }
                                                        />
                                                        Done
                                                    </label>
                                                    <label>
                                                        <input
                                                            type="radio"
                                                            name={`feedback-${req.id}`}
                                                            value="Need Changes"
                                                            checked={fb.classification === "Need Changes"}
                                                            onChange={(e) =>
                                                                updateFeedbackItem(obj.id, req.id, 'classification', e.target.value)
                                                            }
                                                        />
                                                        Need Changes
                                                    </label>
                                                </div>
                                                {fb.classification === "Need Changes" && (
                                                    <div className="alteration-link">
                                                        <Link to="/step5" className="requirements-link">
                                                            Edit Requirement in Step 5
                                                        </Link>
                                                    </div>
                                                )}
                                            </div>
                                        );
                                    })}
                                </div>
                            ) : (
                                <p className="empty-message">No requirements available for this objective.</p>
                            )}
                        </div>
                    ))
                ) : (
                    <p className="empty-message">No objectives available.</p>
                )}

                <NavigationButtons
                    onSave={handleSave}
                    nextPath="/finalSummary"
                    previousPath="/step8"
                    stepNumber={currentStep}
                />
            </div>
        </>
    );
};

export default FeedbackPage;
