import React, {useState} from 'react';
import { Link } from 'react-router-dom';
import '../css/Navbar.css';
import LoadProjectModal from "./LoadProjectModal";

const NavbarOtherPages = () => {
    const [modalOpen, setModalOpen] = useState(false);

    const openModal = () => {
        setModalOpen(true);
    };

    const closeModal = () => {
        setModalOpen(false);
    };

    return (
        <nav className="navbar navbar-expand-lg navbar-dark" style={{ background: 'linear-gradient(135deg, #4e54c8, #8f94fb)' }}>
            <div className="container-fluid">
                <Link className="navbar-brand" to="/">IMS Approach Tool</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav ms-auto">
                        <li className="nav-item">
                            <Link className="nav-link" to="/">Homepage</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/catalog">Catalog</Link>
                        </li>
                        <li className="nav-item">
                            <button onClick={openModal} className='nav-link upload-link'>
                                Upload
                            </button>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/step1">New Project</Link>
                        </li>
                    </ul>
                </div>
            </div>
            {modalOpen && <LoadProjectModal isOpen={modalOpen} onClose={closeModal} />}
        </nav>
    );
};

export default NavbarOtherPages;
