import React, {useContext} from 'react';
import { useNavigate } from 'react-router-dom';
import DownloadButton from './DownloadButton';
import '../css/NavigationButtons.css';
import {ApproachContext} from "../context/ApproachContext";

const NavigationButtons = ({ onSave, nextPath, previousPath, onBeforeDownload, stepNumber }) => {
    const navigate = useNavigate();
    const { updateMaxStepCompleted } = useContext(ApproachContext);

    const handleNext = () => {
        if (onSave) onSave();
        updateMaxStepCompleted(stepNumber);
        if (nextPath) navigate(nextPath);
    };

    const handlePrevious = () => {
        if (onSave) onSave();
        if (previousPath) navigate(previousPath);
    };

    return (
        <div className="navigation-buttons">
            {previousPath && (
                <button className="nav-button" onClick={handlePrevious}>
                    Previous
                </button>
            )}
            <DownloadButton inline onBeforeDownload={onBeforeDownload} />
            {nextPath && (
                <button className="nav-button" onClick={handleNext}>
                    Next
                </button>
            )}
        </div>
    );
};

export default NavigationButtons;
