import React, { useContext, useState, useEffect } from 'react';
import { ApproachContext } from '../context/ApproachContext';
import NavigationButtons from '../common/NavigationButtons';
import DynamicList from '../common/DynamicList';
import Navbar from "../common/Navbar";
import '../css/ProjectIdentificationPage.css';
import ProgressBar from "../common/ProgressBar";
import {useUnsavedChanges} from "../context/useUnsavedChanges";

const ProjectIdentificationPage = () => {
    const { approachData, updateStepData, updateMaxStepCompleted } = useContext(ApproachContext);
    const [projectName, setProjectName] = useState('');
    const [generalObjective, setGeneralObjective] = useState('');
    const [projectArea, setProjectArea] = useState('');
    const [stakeholders, setStakeholders] = useState([]);
    const currentStep = 1;
    const totalPages = 9;
    const maxStep = approachData.maxStepCompleted || 0;
    useUnsavedChanges(currentStep, maxStep);

    useEffect(() => {
        if (approachData.step1) {
            setProjectName(approachData.step1.projectName || '');
            setGeneralObjective(approachData.step1.generalObjective || '');
            setProjectArea(approachData.step1.projectArea || '');
            setStakeholders(approachData.step1.stakeholders || []);
        }
    }, [approachData.step1]);

    const handleSave = () => {
        updateStepData('step1', { projectName, generalObjective, projectArea, stakeholders });
        updateMaxStepCompleted(1);
    };

    return (
        <>
            <Navbar currentStep={currentStep}/>
            <ProgressBar currentStep={currentStep} totalSteps={totalPages}/>
            <div className="page-container start-page">
                <h2>1. Project Identification</h2>
                <p>Enter the initial project information</p>
                <div className="form-group">
                    <label>Project Name</label>
                    <input
                        type="text"
                        value={projectName}
                        onChange={(e) => setProjectName(e.target.value)}
                        placeholder="Insert project name"
                    />
                </div>
                <div className="form-group">
                    <label>General Objective of the IMS</label>
                    <input
                        type="text"
                        value={generalObjective}
                        onChange={(e) => setGeneralObjective(e.target.value)}
                        placeholder="Ex: Improving the user experience"
                    />
                </div>
                <div className="form-group">
                    <label>Project Field</label>
                    <input
                        type="text"
                        value={projectArea}
                        onChange={(e) => setProjectArea(e.target.value)}
                        placeholder="Ex: Education, Entertainment, etc."
                    />
                </div>
                <div className="form-group">
                    <label>Stakeholders</label>
                    <DynamicList
                        items={stakeholders}
                        setItems={setStakeholders}
                        placeholder="Name of stakeholder"
                    />
                </div>

                <NavigationButtons
                    onSave={handleSave}
                    onBeforeDownload={handleSave}
                    nextPath="/step2"
                    previousPath={null}
                    stepNumber={currentStep}
                />
            </div>
        </>
    );
};

export default ProjectIdentificationPage;
