import React, { useContext, useState, useEffect } from 'react';
import { ApproachContext } from '../context/ApproachContext';
import NavigationButtons from '../common/NavigationButtons';
import { v4 as uuidv4 } from 'uuid';
import '../css/ObjectivesRefinementPage.css';
import Navbar from "../common/Navbar";
import ProgressBar from "../common/ProgressBar";
import {useUnsavedChanges} from "../context/useUnsavedChanges";

const ObjectivesRefinementPage = () => {
    const { approachData, updateStepData } = useContext(ApproachContext);
    const [objectives, setObjectives] = useState([]);
    const currentStep = 4;
    const totalPages = 9;
    const maxStep = approachData.maxStepCompleted || 0;
    let approachDataVariable = approachData.step3;
    useUnsavedChanges(currentStep, maxStep);

    useEffect(() => {
        if (approachData.step4?.objectives){
            setObjectives(approachData.step4.objectives.map(obj => ({
                ...obj,
                subObjectives: obj.subObjectives || []
            })));
            approachDataVariable = approachData.step4;
        }
        else if (approachData.step3?.objectives) {
            setObjectives(approachData.step3.objectives.map(obj => ({
                ...obj,
                subObjectives: obj.subObjectives || []
            })));
            approachDataVariable = approachData.step3;
        }
    }, [approachDataVariable]);

    const handleSave = () => {
        updateStepData('step4', { objectives });

        const mergeSubObjectives = (existingObjectives, newObjectives) => {
            return existingObjectives.map(existingObj => {
                const newObj = newObjectives.find(obj => obj.id === existingObj.id);
                if (newObj) {
                    const existingSubObjectives = existingObj.subObjectives || [];
                    const updatedSubObjectives = [...existingSubObjectives];

                    newObj.subObjectives.forEach(newSub => {
                        if (!existingSubObjectives.some(sub => sub.id === newSub.id)) {
                            updatedSubObjectives.push(newSub);
                        }
                    });

                    return { ...existingObj, subObjectives: updatedSubObjectives };
                }
                return existingObj;
            });
        };

        if (approachData.step5.objectives?.length > 0) {
            updateStepData('step5', {
                objectives: mergeSubObjectives(approachData.step5.objectives || [], objectives),
            });
        }
        if (approachData.step6.objectives?.length > 0) {
            updateStepData('step6', {
                objectives: mergeSubObjectives(approachData.step6.objectives || [], objectives),
            });
        }
        if (approachData.step7.objectives?.length > 0) {
            updateStepData('step7', {
                objectives: mergeSubObjectives(approachData.step7.objectives || [], objectives),
            });
        }
        if (approachData.step8.objectives?.length > 0) {
            updateStepData('step8', {
                objectives: mergeSubObjectives(approachData.step8.objectives || [], objectives),
            });
        }
        if (approachData.step9.objectives?.length > 0) {
            updateStepData('step9', {
                objectives: mergeSubObjectives(approachData.step9.objectives || [], objectives),
            });
        }
    };


    const updateObjective = (objectiveId, field, value) => {
        setObjectives(prevState =>
            prevState.map(obj =>
                obj.id === objectiveId ? { ...obj, [field]: value } : obj
            )
        );
    };

    const addSubObjective = (objectiveId) => {
        const newSubObjective = { id: uuidv4(), text: '' };
        setObjectives(prevState =>
            prevState.map(obj =>
                obj.id === objectiveId
                    ? { ...obj, subObjectives: [...obj.subObjectives, newSubObjective] }
                    : obj
            )
        );
    };

    const updateSubObjective = (objectiveId, subObjId, value) => {
        setObjectives(prevState =>
            prevState.map(obj =>
                obj.id === objectiveId
                    ? {
                        ...obj,
                        subObjectives: obj.subObjectives.map(sub =>
                            sub.id === subObjId ? { ...sub, text: value } : sub
                        )
                    }
                    : obj
            )
        );
    };

    const removeSubObjective = (objectiveId, subObjId) => {
        setObjectives(prevState =>
            prevState.map(obj =>
                obj.id === objectiveId
                    ? { ...obj, subObjectives: obj.subObjectives.filter(sub => sub.id !== subObjId) }
                    : obj
            )
        );
    };

    return (
        <>
            <Navbar currentStep={currentStep}/>
            <ProgressBar currentStep={currentStep} totalSteps={totalPages}/>
            <div className="page-container refinement-page">
                <h2>4. Refining the Goals</h2>
                <p>Review and refine the goals you set earlier.</p>

                {objectives.length > 0 ? (
                    objectives.map((objective, index) => (
                        <div key={objective.id} className="objective-card">
                            <div className="objective-header">
                                <h3>Goal Number {index + 1}</h3>
                            </div>

                            <div className="sub-objectives-section">
                                <label>Goal:</label>
                                <input
                                    type="text"
                                    value={objective.goal}
                                    onChange={(e) => updateObjective(objective.id, 'goal', e.target.value)}
                                    placeholder="Edit the main goal"
                                />
                                {objective.subGoal && (
                                    <>
                                        <label>Sub Goal:</label>
                                        <input
                                            type="text"
                                            value={objective.subGoal}
                                            onChange={(e) => updateObjective(objective.id, 'subGoal', e.target.value)}
                                            placeholder="Edit the sub-goal"
                                        />
                                    </>
                                )}
                                {objective.fromCatalog && (
                                    <p className="catalog-source">Source: {objective.catalogSource}</p>
                                )}
                                <h4>Sub-Goals:</h4>
                                {objective.subObjectives.length > 0 ? (
                                    objective.subObjectives.map(sub => (
                                        <div key={sub.id} className="sub-objective-item">
                                            <input
                                                type="text"
                                                value={sub.text}
                                                onChange={(e) => updateSubObjective(objective.id, sub.id, e.target.value)}
                                                placeholder="Insert sub-goal"
                                            />
                                            <button className="remove-btn" onClick={() => removeSubObjective(objective.id, sub.id)}>
                                                Remove
                                            </button>
                                        </div>
                                    ))
                                ) : (
                                    <p className="no-sub-objectives">No sub-goals have been defined.</p>
                                )}
                                <button className="add-btn" onClick={() => addSubObjective(objective.id)}>
                                    Add Sub-Goal
                                </button>
                            </div>
                        </div>
                    ))
                ) : (
                    <p className="empty-message">No goals were defined in the previous step.</p>
                )}

                <NavigationButtons onSave={handleSave} nextPath="/step5" previousPath="/step3" stepNumber={currentStep} onBeforeDownload={handleSave}/>
            </div>
        </>
    );
};

export default ObjectivesRefinementPage;
