import React, { useContext, useState, useEffect } from 'react';
import { ApproachContext } from '../context/ApproachContext';
import NavigationButtons from '../common/NavigationButtons';
import CatalogModal from '../common/CatalogModal';
import { v4 as uuidv4 } from 'uuid';
import '../css/ObjectivesCollectionPage.css';
import Navbar from "../common/Navbar";
import ProgressBar from "../common/ProgressBar";
import { useUnsavedChanges } from "../context/useUnsavedChanges";

const ObjectivesCollectionPage = () => {
    const { approachData, updateStepData } = useContext(ApproachContext);
    const currentStep = 3;
    const totalPages = 9;
    const maxStep = approachData.maxStepCompleted || 0;
    useUnsavedChanges(currentStep, maxStep);

    const [objectives, setObjectives] = useState([]);
    const [showCatalogModal, setShowCatalogModal] = useState(false);

    useEffect(() => {
        if (approachData.step3?.objectives) {
            setObjectives(approachData.step3.objectives);
        }
    }, [approachData.step3]);

    const handleSave = () => {
        const allObjectivesValid = objectives.every(obj => obj.stakeholders && obj.stakeholders.length > 0);
        if (!allObjectivesValid) {
            alert("Each Goal must have at least one associated stakeholder.");
            return;
        }

        updateStepData('step3', { objectives });

        const mergeObjectives = (existingObjectives, newObjectives) => {
            const merged = [...existingObjectives];

            newObjectives.forEach(newObj => {
                if (!existingObjectives.some(obj => obj.id === newObj.id)) {
                    merged.push(newObj);
                }
            });

            return merged;
        };

        if (approachData.step4.objectives?.length > 0) {
            updateStepData('step4', {
                objectives: mergeObjectives(approachData.step4.objectives || [], objectives),
            });
        }
        if (approachData.step5.objectives?.length > 0) {
            updateStepData('step5', {
                objectives: mergeObjectives(approachData.step5.objectives || [], objectives),
            });
        }
        if (approachData.step6.objectives?.length > 0) {
            updateStepData('step6', {
                objectives: mergeObjectives(approachData.step6.objectives || [], objectives),
            });
        }
        if (approachData.step7.objectives?.length > 0) {
            updateStepData('step7', {
                objectives: mergeObjectives(approachData.step7.objectives || [], objectives),
            });
        }
        if (approachData.step8.objectives?.length > 0) {
            updateStepData('step8', {
                objectives: mergeObjectives(approachData.step8.objectives || [], objectives),
            });
        }
        if (approachData.step9.objectives?.length > 0) {
            updateStepData('step9', {
                objectives: mergeObjectives(approachData.step9.objectives || [], objectives),
            });
        }
    };

    const addObjectiveManually = () => {
        const newObjective = {
            id: uuidv4(),
            goal: '',
            fromCatalog: false,
            catalogSource: null,
            stakeholders: []
        };
        setObjectives([...objectives, newObjective]);
    };

    const handleCatalogSelect = (catalogItem) => {
        const subObjectiveId = uuidv4();
        const newObjective = {
            id: uuidv4(),
            goal: catalogItem.goal,
            fromCatalog: true,
            catalogSource: catalogItem.title,
            subObjectives: [
                {
                    "id": subObjectiveId,
                    "text": catalogItem.subGoal
                }
            ],
            requirements: getRequirements(catalogItem.requirements, subObjectiveId),
            userStory: catalogItem.userStory,
            bddScenario: catalogItem.bddScenario,
            stakeholders: []
        };
        setObjectives([...objectives, newObjective]);
        setShowCatalogModal(false);
    };

    const removeObjective = (objectiveId) => {
        setObjectives(objectives.filter(obj => obj.id !== objectiveId));
    };

    const updateObjectiveField = (objectiveId, field, value) => {
        setObjectives(objectives.map(obj =>
            obj.id === objectiveId ? { ...obj, [field]: value } : obj
        ));
    };

    const getRequirements = (requirements, subObjectiveId) => {
        return requirements.map(item => setRequirements(item, subObjectiveId));
    };

    const setRequirements = (item, subObjectiveId) => {
        return { id: uuidv4(), text: item.text, subObjectiveId: subObjectiveId, type: item.type};
    };

    const addStakeholderToObjective = (objectiveId, stakeholder) => {
        setObjectives(objectives.map(obj =>
            obj.id === objectiveId && !obj.stakeholders.includes(stakeholder)
                ? { ...obj, stakeholders: [...obj.stakeholders, stakeholder] }
                : obj
        ));
    };

    const removeStakeholderFromObjective = (objectiveId, stakeholder) => {
        setObjectives(objectives.map(obj =>
            obj.id === objectiveId
                ? { ...obj, stakeholders: obj.stakeholders.filter(s => s !== stakeholder) }
                : obj
        ));
    };

    return (
        <>
            <Navbar currentStep={currentStep} />
            <ProgressBar currentStep={currentStep} totalSteps={totalPages} />
            <div className="page-container objectives-page">
                <h2>3. Start of Goal Collection</h2>

                <div className="objectives-actions">
                    <button className="add-btn" onClick={addObjectiveManually}>
                        Add Goal Manually
                    </button>
                    <button className="catalog-btn" onClick={() => setShowCatalogModal(true)}>
                        Select from Catalog
                    </button>
                </div>

                {objectives.length > 0 ? (
                    objectives.map((objective, index) => (
                        <div key={objective.id} className={`objective-card ${objective.fromCatalog ? 'from-catalog' : ''}`}>
                            <div className="objective-header">
                                <h3>Goal Number {index + 1}</h3>
                                <button className="remove-btn" onClick={() => removeObjective(objective.id)}>
                                    Delete
                                </button>
                            </div>
                            <div className="form-group">
                                <label>Goal:</label>
                                <input
                                    type="text"
                                    value={objective.goal}
                                    onChange={(e) => updateObjectiveField(objective.id, 'goal', e.target.value)}
                                    placeholder="Select from Catalog"
                                />
                                {objective.fromCatalog && (
                                    <p className="catalog-source">Source: {objective.catalogSource}</p>
                                )}
                            </div>

                            <div className="form-group">
                                <label>Stakeholders:</label>
                                <div className="stakeholder-list">
                                    {objective.stakeholders.map(stakeholder => (
                                        <span key={stakeholder} className="stakeholder-tag">
                                            {stakeholder}
                                            <button className="remove-stakeholder-btn" onClick={() => removeStakeholderFromObjective(objective.id, stakeholder)}></button>
                                        </span>
                                    ))}
                                </div>
                                <select
                                    onChange={(e) => addStakeholderToObjective(objective.id, e.target.value)}
                                    defaultValue=""
                                >
                                    <option value="" disabled>Select Stakeholder</option>
                                    {approachData.step1.stakeholders.map(stk => (
                                        <option key={stk} value={stk}>
                                            {stk}
                                        </option>
                                    ))}
                                </select>
                            </div>
                        </div>
                    ))
                ) : (
                    <p className="empty-message">No goals added yet.</p>
                )}

                <NavigationButtons onSave={handleSave} nextPath="/step4" previousPath="/step2" stepNumber={currentStep} onBeforeDownload={handleSave} />

                {showCatalogModal && (
                    <CatalogModal
                        onClose={() => setShowCatalogModal(false)}
                        onSelectPattern={handleCatalogSelect}
                    />
                )}
            </div>
        </>
    );
};

export default ObjectivesCollectionPage;